<template>
	<view class="activity-page">
		<!-- È°∂ÈÉ®Á≠õÈÄâÊ†è -->
		<view class="filter-bar">
			<scroll-view class="filter-scroll" scroll-x="true" show-scrollbar="false">
				<view class="filter-item"
					:class="{ active: currentFilter === 'all' }"
					@tap="setFilter('all')">
					ÂÖ®ÈÉ®
				</view>
				<view class="filter-item"
					:class="{ active: currentFilter === 'public' }"
					@tap="setFilter('public')">
					üåç ÂÖ¨ÂºÄÊ¥ªÂä®
				</view>
				<view class="filter-item"
					:class="{ active: currentFilter === 'team' }"
					@tap="setFilter('team')">
					üë• Âõ¢ÈòüÊ¥ªÂä®
				</view>
				<view class="filter-item"
					:class="{ active: currentFilter === 'my' }"
					@tap="setFilter('my')">
					üìù ÊàëÁöÑÊä•Âêç
				</view>
			</scroll-view>
		</view>

		<!-- Ê¥ªÂä®Á±ªÂûãÁ≠õÈÄâ -->
		<view class="type-filter" v-if="showTypeFilter">
			<scroll-view class="type-scroll" scroll-x="true" show-scrollbar="false">
				<view class="type-item"
					:class="{ active: currentType === '' }"
					@tap="setType('')">
					ÂÖ®ÈÉ®Á±ªÂûã
				</view>
				<view class="type-item"
					v-for="(typeInfo, type) in activityTypes"
					:key="type"
					:class="{ active: currentType === type }"
					@tap="setType(type)">
					{{ typeInfo.icon }} {{ typeInfo.name }}
				</view>
			</scroll-view>
		</view>

		<!-- Ê¥ªÂä®ÂàóË°® -->
		<view class="activity-list">
			<view v-if="loading" class="loading-state">
				<text>Âä†ËΩΩ‰∏≠...</text>
			</view>
			
			<view v-else-if="activities.length > 0">
				<view class="activity-card" 
					v-for="activity in activities" 
					:key="activity.id"
					@tap="viewActivity(activity)">
					
					<!-- Ê¥ªÂä®Â§¥ÈÉ® -->
					<view class="activity-header">
						<view class="activity-type">
							{{ getTypeInfo(activity.activity_type).icon }}
						</view>
						<view class="activity-status" :style="{ color: getStatusInfo(activity).color }">
							{{ getStatusInfo(activity).text }}
						</view>
					</view>
					
					<!-- Ê¥ªÂä®ÂÜÖÂÆπ -->
					<view class="activity-content">
						<text class="activity-title">{{ activity.title }}</text>
						<text class="activity-desc" v-if="activity.description">{{ activity.description }}</text>
						
						<view class="activity-details">
							<view class="detail-item">
								<text class="detail-icon">üìÖ</text>
								<text class="detail-text">{{ formatDate(activity.start_time) }}</text>
							</view>
							
							<view class="detail-item" v-if="activity.location">
								<text class="detail-icon">üìç</text>
								<text class="detail-text">{{ activity.location }}</text>
							</view>
							
							<view class="detail-item">
								<text class="detail-icon">üë•</text>
								<text class="detail-text">
									{{ activity.registration_count || 0 }}{{ activity.max_participants ? `/${activity.max_participants}` : '' }} ‰∫∫ÂèÇ‰∏é
								</text>
							</view>
						</view>
						
						<view class="activity-footer">
							<text class="creator">Áî± {{ activity.creator_name }} ÂàõÂª∫</text>
						</view>
					</view>
				</view>
			</view>
			
			<!-- Á©∫Áä∂ÊÄÅ -->
			<view class="empty-state" v-else>
				<text class="empty-icon">üìÖ</text>
				<text class="empty-title">ÊöÇÊó†Ê¥ªÂä®</text>
				<text class="empty-subtitle">Âø´Êù•ÂàõÂª∫Á¨¨‰∏Ä‰∏™Ê¥ªÂä®ÂêßÔºÅ</text>
			</view>
		</view>

		<!-- ÂàõÂª∫Ê¥ªÂä®ÊåâÈíÆ -->
		<view class="create-btn" @tap="createActivity">
			<text class="create-icon">+</text>
		</view>
	</view>
</template>

<script>
	import { activityApi } from '../../api/index.js'
	import { showSuccess, showError, formatDate } from '../../utils/index.js'

	export default {
		data() {
			return {
				activities: [],
				activityTypes: {}, // Ê¥ªÂä®Á±ªÂûãÊï∞ÊçÆÔºå‰ªéAPIÂä†ËΩΩ
				loading: false,
				currentFilter: 'all',
				currentType: '',
				showTypeFilter: false
			}
		},

		onLoad() {
			this.loadInitialData()
		},

		onShow() {
			this.loadInitialData()
		},

		onPullDownRefresh() {
			this.loadInitialData().finally(() => {
				uni.stopPullDownRefresh()
			})
		},

		methods: {
			// Âä†ËΩΩÂàùÂßãÊï∞ÊçÆ
			async loadInitialData() {
				await Promise.all([
					this.loadActivities(),
					this.loadActivityTypes()
				])
			},

			// Âä†ËΩΩÊ¥ªÂä®Á±ªÂûã
			async loadActivityTypes() {
				try {
					// ‰ªéÂêéÁ´ØAPIËé∑ÂèñÁúüÂÆûÊ¥ªÂä®Á±ªÂûãÊï∞ÊçÆ
					const response = await activityApi.getTypes()
					if (response.success) {
						// ËΩ¨Êç¢ÂêéÁ´ØÊï∞ÊçÆÊ†ºÂºè‰∏∫ÂâçÁ´ØÈúÄË¶ÅÁöÑÊ†ºÂºè
						const typesData = {}
						const types = response.data || []
						
						types.forEach(type => {
							// ‰∏∫ÊØè‰∏™Á±ªÂûãÊ∑ªÂä†ÂõæÊ†á
							const icon = this.getTypeIcon(type.id)
							typesData[type.id] = {
								icon: icon,
								name: type.name
							}
						})
						
						this.activityTypes = typesData
						console.log(`ÊàêÂäüÂä†ËΩΩ ${types.length} ‰∏™Ê¥ªÂä®Á±ªÂûã`)
					} else {
						throw new Error(response.message || 'Ëé∑ÂèñÊ¥ªÂä®Á±ªÂûãÂ§±Ë¥•')
					}
				} catch (error) {
					console.error('Âä†ËΩΩÊ¥ªÂä®Á±ªÂûãÂ§±Ë¥•:', error)
					// ÈôçÁ∫ßÂà∞ÈªòËÆ§Á±ªÂûã
					this.activityTypes = {
						other: { icon: 'üìÖ', name: 'ÂÖ∂‰ªñ' }
					}
				}
			},

			// Ê†πÊçÆÁ±ªÂûãIDËé∑ÂèñÂØπÂ∫îÂõæÊ†á
			getTypeIcon(typeId) {
				const iconMap = {
					meeting: 'üíº',
					event: 'üéâ', 
					training: 'üìö',
					social: 'üçΩÔ∏è',
					sports: '‚öΩ',
					travel: 'üèñÔ∏è',
					workshop: 'üîß',
					conference: 'üé§',
					other: 'üìÖ'
				}
				return iconMap[typeId] || 'üìÖ'
			},

			// ËÆæÁΩÆÁ≠õÈÄâÊù°‰ª∂
			setFilter(filter) {
				this.currentFilter = filter
				this.showTypeFilter = filter !== 'all'
				this.loadActivities()
			},

			// ËÆæÁΩÆÁ±ªÂûãÁ≠õÈÄâ
			setType(type) {
				this.currentType = type
				this.loadActivities()
			},

			// Âä†ËΩΩÊ¥ªÂä®ÂàóË°®
			async loadActivities() {
				this.loading = true
				try {
					const params = this.buildParams()
					const response = await activityApi.getList(params)
					if (response.success) {
						// ‰øÆÂ§çÔºöÊ¥ªÂä®Êï∞ÊçÆÂú® response.data.activities ‰∏≠
						const activities = response.data.activities || response.data || []
						this.activities = Array.isArray(activities) ? activities : []
					}
				} catch (error) {
					console.error('Âä†ËΩΩÊ¥ªÂä®Â§±Ë¥•:', error)
					showError('Âä†ËΩΩÊ¥ªÂä®Â§±Ë¥•')
				} finally {
					this.loading = false
				}
			},

			// ÊûÑÂª∫ËØ∑Ê±ÇÂèÇÊï∞
			buildParams() {
				const params = {}
				
				if (this.currentFilter === 'public') {
					params.visibility = 'public'
				} else if (this.currentFilter === 'team') {
					params.visibility = 'team'
				} else if (this.currentFilter === 'my') {
					params.my_registrations = true
				}
				
				if (this.currentType) {
					params.activity_type = this.currentType
				}
				
				return params
			},

			// Êü•ÁúãÊ¥ªÂä®ËØ¶ÊÉÖ
			viewActivity(activity) {
				uni.navigateTo({
					url: `/pages/activity-detail/activity-detail?id=${activity.id}`
				})
			},

			// ÂàõÂª∫Ê¥ªÂä®
			createActivity() {
				uni.navigateTo({
					url: '/pages/activity-create/activity-create'
				})
			},

			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(date) {
				return formatDate(date, 'MMÊúàDDÊó• HH:mm')
			},

			// Ëé∑ÂèñÊ¥ªÂä®Áä∂ÊÄÅ‰ø°ÊÅØ
			getStatusInfo(activity) {
				const now = new Date()
				const startTime = new Date(activity.start_time)
				const endTime = new Date(activity.end_time)

				if (now < startTime) {
					return { text: 'Âç≥Â∞ÜÂºÄÂßã', color: '#007aff' }
				} else if (now >= startTime && now <= endTime) {
					return { text: 'ËøõË°å‰∏≠', color: '#ff9500' }
				} else {
					return { text: 'Â∑≤ÁªìÊùü', color: '#8e8e93' }
				}
			},

			// Ëé∑ÂèñÁ±ªÂûã‰ø°ÊÅØ
			getTypeInfo(type) {
				return this.activityTypes[type] || { icon: 'üìÖ', name: 'Êú™Áü•' }
			}
		}
	}
</script>

